@import "styles/variables";
@import "styles/mixins";

.address {
  @include h5($colorDarkest);

  margin: 0.5rem 0 0 0;
  opacity: 0.5;
}

.preview {
  @include resetButton;

  width: 100%;
  height: 35rem;
  background-size: cover;
}

.full {
  display: block;
  margin: 15vh auto 0 auto;
  width: 100rem;
}

.list {
  @include resetList;

  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1.5rem;
  margin-top: 2rem;
}

.subheading {
  margin-top: 5rem;
  text-align: center;
}

.feature {
  @include h8($colorLightest);

  position: relative;
  padding: 5rem 5rem 5rem 25rem;
  background-color: $colorHighlightDark;

  &::before {
    content: "";
    position: absolute;
    top: calc(50% - 10rem / 2);
    left: 7rem;
    width: 10rem;
    height: 10rem;
    background-image: url("../../assets/icons/i_jigsaw.svg");
    background-size: cover;
  }
}

@media (max-width: $adaptiveMax) {
  .preview {
    height: 20rem;
  }

  .feature {
    padding: 5rem 5rem 5rem 20rem;

    &::before {
      top: calc(50% - 7rem / 2);
      left: 5rem;
      width: 7rem;
      height: 7rem;
    }
  }
}
