@import "styles/variables";
@import "styles/mixins";

.wrapper {
  position: relative;
}

.rank {
  @include small($colorLightest);

  position: absolute;
  top: 0;
  right: 0;
  padding: 0.5rem 2rem;
  width: 65%;
  text-align: center;
}

.ceo {
  background-color: #9A275A;
}

.pm {
  background-color: #E36588;
}

.adm {
  color: $colorDarkest;
  background-color: #99EDCC;
}

.engineer {
  background-color: #9AC4F8;
}

.photo {
  width: 100%;
  height: 50rem;
  object-fit: cover;
  object-position: center;
}

.name {
  @include h7($colorDarkest);

  display: block;
  margin: 1rem 0 0 0;
}

.email {
  @include regular($colorDarkest);
  @include link;
}

@media (max-width: $adaptiveMax) {
  .rank {
    top: 41rem;
    width: 100%;
  }

  .photo {
    width: 100%;
    height: 45rem;
    object-fit: cover;
    object-position: center;
  }
}
